export default {
  name: "Login",
  data(){
    return{
      form : {
        username : '',
        password:''
      }
    }
  },
  methods:{
    async onSubmit() {
      await this.$auth.loginWith('local', {
        data: {
          username: this.form.username,
          password: this.form.password
        }
      })
      this.form = {
        username: '',
        password: ''
      }
      this.$router.push({path: '/'})
    }
  }
}
